body {
  margin: 0 auto;
  min-width: 450px;
  max-width: 1000px;
  width: 80%;
  padding: 2px 4px 50px 4px;
}


h3, label {
  display: block;
  margin-top: 25px;
  font-size: 1.15em;
  font-weight: bold;

  &.small-label {
    margin-top: 7px;
    font-size: 1em;
  }
}

h2 {
  display: block;
  margin-top: 25px;
  font-size: 1.30em;
  font-weight: bold;
}


input {
  min-width: 450px;
  width: 100%;
  float: none;
}

input[type=checkbox] {
  height: 20px;
  min-width: 20px;
  width: 20px;
  margin-left: 5px;

  &.settings-checkbox {
    display: inline;
    margin-right: 8px;
    vertical-align: middle;
  }
}

textarea {
  display: block;
  min-width: 450px;
  width: 100%;
  height: 200px;
  margin-bottom: 5px;
  paddin: 1px;
}

.trip-text {
  textarea {
    height: 350px;
  }
}

table {

  td, th {
    padding: 1px;
    padding-right: 4px;
    padding-bottom: 3px;
    vertical-align: middle;
  }

  th {
    font-weight: bold;
  }

  tr:hover {
    background-color: rgba(225, 225, 225, 0.5);
  }

  input[type=number],
  input[type=time] {
    display: inline-block;
    margin-top: 4px;
  }

}

#trip-hotel-text {
  height: 100px;
}

#dates-list {
  input {
    min-width: 155px;
    width: 155px;
  }
}

.extra-settings {
  line-height:35px;
  height: 35px;
}

#save-trip-button {
  padding: 10px 35px;
  margin: 10px 10px 50px 10px;
  font-size: 1.3em;
  font-weight: bold;
}

.room-price,
.stop-input,
#trip-duration,
#trip-price {
  min-width: 75px;
  width: 75px;
}


#addons-list {
  div {

    input {
      display: inline;

      &:first-of-type {
        min-width: 400px;
        width: 50%;
        margin-right: 3px;
      }
      &:last-of-type {
        min-width: 70px;
        width: 70px;
      }
    }


  }

}

.picture-new,
.pdf-new {
  border: 1px solid #988;
  margin-top: 12px;
  padding: 4px;

  input,
  button {
    display: inline;

  }
}

input[type=file] {
  width: 300px;
  min-width: 300px;
  margin-right: 10px;
}

.file-label {
  display: inline-block;
  width: 150px;
  font-weight: bold;
  font-size: 1.12em;
  margin: 8px 4px;
}

.importaint {
  color: red;
}

.picture-list-thumb {
  padding: 7px;
  height: 300px;
}

.picture-info {
  border-top: 1px solid #988;
  font-size: 0.7em;
  padding: 7px;
  color: grey;
  a {
    color: grey;

    &:visited {
      color: grey
    }
  }
}
